<template>
  <div>
    <div>
      <el-row class="pad-bt05 box v-c">
        <el-col :span="12">
          <router-link to="/phone">
            <el-button class="btn" type="danger">返回</el-button>
          </router-link>
        </el-col>
        <el-col :span="12" class="tx-r">

        </el-col>
      </el-row>
      <h3>筛选条件</h3>
      <div class="condition">
        <span class="c_name">系统</span>
        <el-radio class="radio" v-model="system" label="1">iOS</el-radio>
        <el-radio class="radio" v-model="system" label="2">Android</el-radio>
      </div>
      <div class="condition">
        <span class="c_name">系统版本</span>
        <el-radio class="radio" v-model="version" label="1">8.1</el-radio>
        <el-radio class="radio" v-model="version" label="2">10</el-radio>
      </div>
      <div class="condition">
        <span class="c_name">所在地</span>
        <el-radio class="radio" v-model="location" label="1">上海</el-radio>
        <el-radio class="radio" v-model="location" label="2">武汉</el-radio>
        <el-radio class="radio" v-model="location" label="3">其他</el-radio>
      </div>
      <div class="condition">
        <span class="c_name">状态</span>
        <el-radio class="radio" v-model="status" label="1">正常</el-radio>
        <el-radio class="radio" v-model="status" label="2">异常</el-radio>
      </div>
      <div class="mar-t1">
        <el-row>
          <el-col :span="12" class="tx-c">
            <el-button type="primary">确认筛选</el-button>
          </el-col>
          <el-col :span="12" class="tx-c">
            <el-button type="warning">取消筛选</el-button>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
<script>
      export default{
        data(){
            return{
                system:'1',
                version:'1',
                location:'1',
                status:'1'
            }
        }
    }
</script>



