<template>
  <div>
    <el-row class="pad-bt05 box v-c">
      <el-col :span="24">
        <router-link to="/phone">
          <el-button class="btn" type="danger">返回</el-button>
        </router-link>
      </el-col>
    </el-row>
    <div v-for="(p,key) in phoneData">
      <div class="device_main box v-c middle">
        <div :class="['device_img',p.phone_img]"></div>
        <h2 class="mar-t1">{{p.phone_name}}</h2>
        <span class="mar-t05">{{p.phone_system}} / {{p.system_version}}</span>
        <el-row class="box h-c mar-t05">
          <el-col :span="14">
            <el-select v-model="p.holder" placeholder="请选择" @change="test($event)">
              <el-option v-for="item in options"
                         v-if="item.label!=p.holder"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-col>
        </el-row>
        <span class="mar-t05">{{p.address}}/{{p.phone_status==1?"正常":"异常"}}</span>
      </div>
      <div class="device_detail">
        <div class="bb-d tx-c">
          <h3>手机详情</h3>
        </div>
        <el-row class="detail_item bb-d">
          <el-col :span="8">基本参数</el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">品牌</span>
            <span class="pad-b05 pad-t05">型号</span>
            <span class="pad-b05 pad-t05">具体型号</span>
            <span class="pad-b05 pad-t05">上市时间</span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">苹果（Apple)</span>
            <span class="pad-b05 pad-t05">iPhone 7</span>
            <span class="pad-b05 pad-t05">A1660</span>
            <span class="pad-b05 pad-t05">2016年9月</span>
          </el-col>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8">系统信息</el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">系统</span>
            <span class="pad-b05 pad-t05">系统版本号</span>
            <span class="pad-b05 pad-t05">定制系统</span>
            <span class="pad-b05 pad-t05">定制系统版本号</span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">iOS</span>
            <span class="pad-b05 pad-t05">iOS 10.0</span>
            <span class="pad-b05 pad-t05">--</span>
            <span class="pad-b05 pad-t05">--</span>
          </el-col>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8">屏幕信息</el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">尺寸（英寸）</span>
            <span class="pad-b05 pad-t05">分辨率</span>
            <span class="pad-b05 pad-t05">ppi</span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">4.7</span>
            <span class="pad-b05 pad-t05">750x1334</span>
            <span class="pad-b05 pad-t05">326</span>
          </el-col>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8">其他硬件信息</el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">运行内存(G)</span>
            <span class="pad-b05 pad-t05">充电口</span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">2</span>
            <span class="pad-b05 pad-t05">Lightning数据接口</span>
          </el-col>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8">使用信息</el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">持有人</span>
            <span class="pad-b05 pad-t05">所在地</span>
            <span class="pad-b05 pad-t05">状态</span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">{{p.holder}}</span>
            <span class="pad-b05 pad-t05">上海</span>
            <span class="pad-b05 pad-t05">正常</span>
          </el-col>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8">购买信息</el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">购买金额</span>
            <span class="pad-b05 pad-t05">购买时间</span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">￥4798</span>
            <span class="pad-b05 pad-t05">2017-01-01</span>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
<script>
  import '../../assets/css/deatil.css'
  export default{
    data(){
      return {
        phoneData: [],
        options: [{
          value: '李林',
          label: '李林'
        }, {
          value: '尹瑞',
          label: '尹瑞'
        }, {
          value: '朱安智',
          label: '朱安智'
        }, {
          value: '陈文凭',
          label: '陈文凭'
        }],
        device_list:{}
      }
    },
    methods: {
      test(holder){
        this.phoneData[0].holder=holder;
      }
    },
    created(){
      this.phoneData[0] = this.$route.params;
    }
  }
</script>



